@use './variables' as *;
@use './functions' as *;
@use 'sass:math';
@use 'sass:map';

@mixin center-left {
  height: 101%;
  left: 0; // Aligns to the left edge
  object-fit: cover;
  object-position: left;
  position: absolute;
  top: 50%; // Vertically centers
  transform: translateY(-50%); // Adjusts for vertical centering
  vertical-align: top;
  width: 101%;
}

@mixin center-center {
  height: 101%;
  left: 50%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  vertical-align: top;
  width: 101%;
}

@mixin center-right {
  height: 101%;
  object-fit: cover;
  object-position: right;
  position: absolute;
  right: 0; // Aligns to the right edge
  top: 50%; // Vertically centers
  transform: translateY(-50%); // Adjusts for vertical centering
  vertical-align: top;
  width: 101%;
}

@mixin top-left {
  height: 101%;
  left: 0; // Aligns to the left edge
  object-fit: cover;
  object-position: left;
  position: absolute;
  top: 0; // Aligns to the top edge
  transform: none; // No transformation needed
  vertical-align: top;
  width: 101%;
}

@mixin top-center {
  height: 101%;
  left: 50%; // Horizontally centers
  object-fit: cover;
  position: absolute;
  top: 0; // Aligns to the top edge
  transform: translateX(-50%); // Adjusts for horizontal centering
  vertical-align: top;
  width: 101%;
}

@mixin top-right {
  height: 101%;
  right: 0; // Aligns to the right edge
  object-fit: cover;
  object-position: right;
  position: absolute;
  top: 0; // Aligns to the top edge
  transform: none; // No transformation needed
  vertical-align: top;
  width: 101%;
}

@mixin bottom-left {
  height: 101%;
  bottom: 0; // Aligns to the bottom edge
  left: 0; // Aligns to the left edge
  object-fit: cover;
  object-position: left;
  position: absolute;
  transform: none; // No transformation needed
  vertical-align: top;
  width: 101%;
}

@mixin bottom-center {
  height: 101%;
  bottom: 0; // Aligns to the bottom edge
  left: 50%; // Horizontally centers
  object-fit: cover;
  position: absolute;
  transform: translateX(-50%); // Adjusts for horizontal centering
  vertical-align: top;
  width: 101%;
}

@mixin bottom-right {
  height: 101%;
  bottom: 0; // Aligns to the bottom edge
  right: 0; // Aligns to the right edge
  object-fit: cover;
  object-position: right;
  position: absolute;
  transform: none; // No transformation needed
  vertical-align: top;
  width: 101%;
}

@mixin bpsm {
  @media #{map.get($mq, 'sm')} {
    @content;
  }
}
@mixin bpmd {
  @media #{map.get($mq, 'md')} {
    @content;
  }
}
@mixin bplg {
  @media #{map.get($mq, 'lg')} {
    @content;
  }
}
@mixin bpxl {
  @media #{map.get($mq, 'xl')} {
    @content;
  }
}
@mixin bpxxl {
  @media #{map.get($mq, 'xxl')} {
    @content;
  }
}

@mixin containerize {
  margin: 0 auto;
  padding: 0 map.get($gap-sizes, 3);
  width: 100%;
  @include bpxl {
    padding: 0 map.get($gap-sizes, 7);
  }
  @include bpxxl {
    padding: 0 map.get($gap-sizes, 9);
  }
}

@mixin noto-sans {
  font-family: 'Noto Sans', sans-serif;
  font-optical-sizing: auto;
}
@mixin noto-serif {
  font-family: 'Noto Serif', serif;
  font-optical-sizing: auto;
}
@mixin noto-mono {
  font-family: 'Noto Serif Mono', monospace;
  font-optical-sizing: auto;
}

@mixin button-variant(
  $backgroundColor,
  $borderColor,
  $textColor,
  $hoverBackgroundColor,
  $hoverTextColor
) {
  background-color: $backgroundColor;
  border-color: $borderColor;
  color: $textColor;
  &:hover {
    background-color: $hoverBackgroundColor;
    color: $hoverTextColor;
  }
}

@mixin make-it-translucent($property, $color) {
  #{$property}: rgba($color, $transparent-val);
}

@mixin display1 {
  @include noto-serif;
  font-weight: 300;
  font-size: fluid-size(
    map.get($font-sizes-min, 'display1'),
    map.get($font-sizes-max, 'display1'),
    $font-sizes-display-min,
    $font-sizes-display-max
  );
}
