@use './../../../styles/imports' as *;
@use 'sass:map';

$this-block: 'video-banner';

.#{$this-block} {
  &-content {
    overflow: hidden;
    position: relative;
    width: 100%;
    video {
      @include center-center;
    }
  }
  &-text {
    background-color: var(--color-bg-semi-best);
  }

  &-scroll {
    // border: map.get($gap-sizes, 1) solid transparent;
    background-color: rgba(map.get($grays, 'gray-0'), 50%);
    border-radius: map.get($gap-sizes, 12);
    height: map.get($gap-sizes, 12);
    // padding: map.get($gap-sizes, 1);
    right: map.get($gap-sizes, 5);
    bottom: 0;
    overflow: hidden;
    button {
      background: radial-gradient(
        ellipse at center,
        rgba(map.get($grays, 'gray-0'), 100%) 0%,
        rgba(map.get($grays, 'gray-0'), 0%) 75%
      );
      animation: video-banner-scroll 1.5s linear infinite;
      pointer-events: none;
      position: relative;
      transform: translate(0, -50%);
    }
    &::after {
      content: '';
      display: block;
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
      z-index: 2;
    }
  }

  .container {
    height: 100svh;
    position: relative;
    z-index: 2;
  }
}

.dark-mode {
  .#{$this-block} {
    &-scroll {
      background-color: rgba(map.get($grays, 'gray-10'), 50%);
      button {
        background: radial-gradient(
          ellipse at center,
          rgba(map.get($grays, 'gray-10'), 100%) 0%,
          rgba(map.get($grays, 'gray-10'), 0%) 75%
        );
      }
    }
  }
}

@keyframes video-banner-scroll {
  0% {
    opacity: 0;
    top: 0;
  }
  25% {
    opacity: 1;
    top: 25%;
  }
  75% {
    opacity: 1;
    top: 75%;
  }
  100% {
    opacity: 0;
    top: 100%;
  }
}
